* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
main {
  /*background-image: linear-gradient(#7c2490, #4036c3);*/
  height: 100%;
  width: 100%;
  padding: 20px;
}

main input {
  height: 6vh;
  width: 40vh;
  padding: 10px;
  border-radius: 10px 25px 10px 25px;
  background-color: rgba(13, 12, 39);
  font-size: 20px;
  color: white;
}
.app {
  width: 100%;
  height: 100%;
}
.weather-details {
  height: 65vh;
  width: 40vh;
  margin-top: 30px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: rgba(255, 0, 0, 0.8);
}
.city {
  margin-top: 40px;
  height: 8vh;
  width: 100%;
  text-align: center;
  font-size: 30px;
  font-weight: 800;
}
.date {
  background-color: none;
  height: 7vh;
  text-align: center;
  font-size: 20px;
  font-weight: 500;
}
.temperature {
  background-color: rgba(0, 0, 0);
  height: 30vh;
  width: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 78px;
  font-weight: 900;
  border-radius: 10px 25px 10px 25px;
}
.weather {
  height: 8vh;
  margin: 15px;
  padding: 10px;
  width: 70%;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
}
.weather p {
  margin-top: 15px;
  font-size: 20px;
}

@media only screen and (min-width: 800px) {
  .app {
    width: 100%;
  }
  main {
    width: 100%;
    height: 100%;
  }
  main input {
    margin-left: 25%;
    width: 50%;
  }
  .weather-details {
    width: 100%;
    height: 80vh;
  }
  .temperature {
    width: 25%;
    height: 60%;
  }
}
